<!-- 个人信息页面 -->
<template>
    <div class="box">

        <el-form :label-position="labelPosition" label-width="80px" :model="personalForm">
            <el-form-item label="头像">
                <el-upload class="avatar-uploader" :action="``" :on-change="imgChange"
                    :show-file-list="false">
                    <img v-if="imageUrl" :src="imageUrl" class="avatar">
                    <i class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
            </el-form-item>
            <el-form-item label="用户名" required>
                <el-input v-model="personalForm.userName"></el-input>
            </el-form-item>
            <el-form-item label="所属公司" required>
                <el-input v-model="personalForm.userCompany"></el-input>
            </el-form-item>
            <el-form-item label="密码" required>
                <el-input v-model="personalForm.password"></el-input>
            </el-form-item>
            <el-form-item label="电话号码" required> 
                <el-input v-model="personalForm.userPhone"></el-input>
            </el-form-item>
            <el-form-item label="性别" required>
                <el-radio-group v-model="sex" class="radio">
                    <el-radio :label="'男'">男</el-radio>
                    <el-radio :label="'女'">女</el-radio>
                </el-radio-group>
            </el-form-item>
            <div class="sure">
                <el-button type="primary" class="sure" @click="updateInfo">确定修改</el-button>
            </div>

        </el-form>
    </div>
</template>

<script>
import { reqUserImg } from '@/api/index';
export default {
    name: 'personalInfo',
    data() {
        //验证电话号码
        var validatePhone = (rule, value, callback) => {
            if (value === '') {
                callback(new Error('请输入电话号码'));
            } else {
                callback()
            }
        };
        //验证用户名
        var validateName = (rule, value, callback) => {
            if (value === '') {
                callback(new Error('请输入用户名'));
            } else {
                callback()
            }
        }
        //验证所属公司
        var validateCompany = (rule, value, callback) => {
            if (value === '') {
                callback(new Error('请输入所属公司'));
            } else {
                callback()
            }
        }
        //验证密码
        var validatePass = (rule, value, callback) => {
            if (value === '') {
                callback(new Error('请输入密码'));
            } else {
                if (this.ruleForm.checkPass !== '') {
                    this.$refs.ruleForm.validateField('checkPass');
                }
                callback();
            }
        };
        return {
            imgFile:null,
            imageUrl:null,
            labelPosition: 'right',
            personalForm: {
                password: "",
                userPhone: "",
                userName: "",
                userCompany: ''
            },
            rules: {
                password: [
                    { validator: validatePass, trigger: 'blur' }
                ],
                userPhone: [
                    { validator: validatePhone, trigger: 'blur' }
                ],
                userName: [
                    { validator: validateName, trigger: 'blur' }
                ],
                userCompany: [
                    { validator: validateCompany, trigger: 'blur' }
                ]
            },
            sex: '男'
        };
    },
    methods: {
        //头像处理
        imgChange(file) {
            this.imgFile=file
            this.imageUrl = URL.createObjectURL(file.raw);
        },
        async uploadImg(file) {  
            try {
                const response = await reqUserImg(localStorage.getItem('id'), file.raw);
                console.log(response);
            } catch (error) {
                console.error(error);
            }
        },
        updateInfo() {
            this.$confirm('此操作将修改个人信息, 是否确认?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                const { userName, userCompany, password, userPhone } = this.personalForm
                const id = localStorage.getItem('id')
                const { sex } = this
                const form = { id, userName, userCompany, password, userPhone, sex }
                this.$store.dispatch("updateInfo", { form, vm: this })
                this.uploadImg(this.imgFile)
                this.$store.dispatch("getUserInfo", localStorage.getItem('id'))
            })

        }
    },
    mounted() {

    }
}
</script>

<style lang="scss" scoped>
.box {
    width: 50%;
    height: 700px;
    margin-left: 200px;
    margin-top: 100px;

    .el-form-item {
        margin-bottom: 50px;

        .avatar-uploader {
            width: 100px;
            height: 100px;
            border: 1px dashed #d9d9d9;
            border-radius: 6px;
            cursor: pointer;
            position: relative;
            overflow: hidden;
        }

        .avatar-uploader .el-upload:hover {
            border-color: #409EFF;
        }

        .avatar-uploader-icon {
            font-size: 28px;
            color: #8c939d;
            width: 100px;
            height: 100px;
            line-height: 100px;
            text-align: center;
        }

        .avatar {
            width: 100px;
            height: 100px;
            display: block;

        }
    }

    .radio {
        transform: scale(1.2);
        margin-left: 50px;
    }

    .sure {
        width: 100px;
        height: 50px;
        margin: 0 auto;
    }
}
</style>